"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Textarea } from "@/components/ui/textarea"
import { 
  ChevronLeft, Loader2, Target, Sparkles, Trash2, 
  Edit3, CheckCircle2, BrainCircuit, Wallet, ArrowRight, History, X, Clock
} from "lucide-react"

export default function UserGoalPage() {
  const router = useRouter()
  const [isLoading, setIsLoading] = useState(true)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [exists, setExists] = useState(false)
  
  // 초기 상태를 'view'로 두되, 데이터가 없으면 'form'으로 보냅니다.
  const [step, setStep] = useState<'view' | 'form' | 'deep_analysis' | 'history'>('view')
  const [aiResult, setAiResult] = useState<string | null>(null)

  const [formData, setFormData] = useState({
    age: "", job: "", capital: "", monthlyIncome: "", fixedExpenses: ""
  })

  const [deepAnalysisData, setDeepAnalysisData] = useState({
    goalTitle: "",
    monthlySaving: "",
    investmentStyle: "공격형", 
    customMessage: ""
  })

  const [reports, setReports] = useState<any[]>([])

  const fetchInitialData = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    if (!token) return router.push("/login")

    try {
      // 1. 기본 목표 정보 조회
      const goalRes = await fetch("http://34.50.7.8:3000/v1/usergoal", {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const goalData = await goalRes.json()

      // 데이터가 실제 존재하는지(result 내부에 값이 있는지) 체크
      if (goalRes.ok && goalData.isSuccess && goalData.result && goalData.result.age) {
        setFormData({
          age: String(goalData.result.age),
          job: goalData.result.job,
          capital: String(goalData.result.capital),
          monthlyIncome: String(goalData.result.monthlyIncome),
          fixedExpenses: String(goalData.result.fixedExpenses || "")
        })
        setExists(true)
        setStep('view') // 데이터가 있으면 무조건 view로 고정
      } else {
        setExists(false)
        setStep('form') // 데이터 없으면 입력 폼으로
      }

      // 2. 히스토리 로드
      const reportRes = await fetch("http://34.50.7.8:3000/v1/usergoal/report/all", {
        method: "POST",
        headers: { "Content-Type": "application/json", "Authorization": `Bearer ${token}` },
        body: JSON.stringify({})
      })
      const reportData = await reportRes.json()
      if (reportRes.ok && reportData.isSuccess) {
        setReports(reportData.result || [])
      }
    } catch (error) { 
      console.error("Fetch Error:", error)
      setStep('form')
    } finally { 
      setIsLoading(false) 
    }
  }, [router])

  useEffect(() => { fetchInitialData() }, [fetchInitialData])

  // 전체 목표 프로필 삭제
  const handleDeleteBaseGoal = async () => {
    if(!confirm("모든 정보를 삭제하시겠습니까?")) return
    
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch("http://34.50.7.8:3000/v1/usergoal", {
        method: "DELETE",
        headers: { "Authorization": `Bearer ${token}` }
      })
      console.log(res)
      const data = await res.json()

      if(res.ok && data.isSuccess) {
        alert("삭제되었습니다.")
        setExists(false)
        setFormData({ age: "", job: "", capital: "", monthlyIncome: "", fixedExpenses: "" })
        setStep('form')
      } else {
        alert(data.message || "삭제 실패")
      }
    } catch (e) {
      alert("통신 오류")
    }
  }

  // 기본 정보 저장/수정
  const handleSaveInfo = async () => {
    const token = localStorage.getItem("accessToken")
    setIsSubmitting(true)

    try {
      const response = await fetch("http://34.50.7.8:3000/v1/usergoal", {
        method: exists ? "PATCH" : "POST",
        headers: { "Content-Type": "application/json", "Authorization": `Bearer ${token}` },
        body: JSON.stringify({
          age: Number(formData.age),
          job: formData.job,
          capital: Number(formData.capital),
          monthlyIncome: Number(formData.monthlyIncome),
          fixedExpenses: Number(formData.fixedExpenses)
        })
      })

      const data = await response.json()
      if (response.ok && data.isSuccess) {
        alert("저장되었습니다.")
        setExists(true)
        setStep('view')
      }
    } catch (e) { alert("통신 오류") }
    finally { setIsSubmitting(false) }
  }

  // 개별 리포트 삭제 (History용)
  const handleDeleteReport = async (reportId: string) => {
    if(!confirm("이 보고서를 삭제하시겠습니까?")) return
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`http://34.50.7.8:3000/v1/usergoal/report?reportId=${reportId}`, {
        method: "DELETE",
        headers: { "Authorization": `Bearer ${token}` }
      })
      if(res.ok) {
        setReports(prev => prev.filter(r => r.reportId !== reportId))
      }
    } catch (e) { alert("삭제 오류") }
  }

  // AI 리포트 생성
  const handleCreateReport = async () => {
    const token = localStorage.getItem("accessToken")
    setIsSubmitting(true)
    try {
      const response = await fetch("http://34.50.7.8:3000/v1/usergoal/report/create", {
        method: "POST",
        headers: { "Content-Type": "application/json", "Authorization": `Bearer ${token}` },
        body: JSON.stringify({ 
          question: `목표: ${deepAnalysisData.goalTitle}. 저축액: ${deepAnalysisData.monthlySaving}`
        })
      })
      const data = await response.json()
      if (response.ok && data.isSuccess) {
        setAiResult(data.result.resultReport)
        fetchInitialData() // 새로고침하여 히스토리에 반영
      }
    } catch (e) { alert("AI 분석 실패") }
    finally { setIsSubmitting(false) }
  }

  if (isLoading) return <div className="min-h-screen flex items-center justify-center font-bold text-muted-foreground animate-pulse">L O A D I N G . . .</div>

  return (
    <div className="min-h-screen bg-background text-foreground">
      <header className="border-b bg-card h-16 flex items-center px-4 sticky top-0 z-50 justify-between">
        <div className="flex items-center">
          <Button variant="ghost" size="icon" onClick={() => step === 'view' ? router.push("/dashboard") : setStep('view')}>
            <ChevronLeft className="h-5 w-5" />
          </Button>
          <h1 className="ml-2 text-sm font-black uppercase tracking-widest italic">Financial Advisor</h1>
        </div>
        {exists && (
          <Button variant="ghost" size="sm" onClick={() => setStep('history')} className="font-bold gap-1 text-xs">
            <History className="h-4 w-4" /> 히스토리
          </Button>
        )}
      </header>

      <main className="container mx-auto max-w-xl px-4 py-10">
        {aiResult && (
          <div className="fixed inset-0 z-[100] bg-black/60 backdrop-blur-sm flex items-center justify-center p-4">
            <Card className="w-full max-w-lg max-h-[80vh] flex flex-col bg-card border-none shadow-2xl">
              <div className="p-5 border-b flex justify-between items-center">
                <div className="flex items-center gap-2 text-primary font-black"><Sparkles className="h-5 w-5" /> AI 분석 리포트</div>
                <Button variant="ghost" size="icon" onClick={() => setAiResult(null)}><X className="h-5 w-5" /></Button>
              </div>
              <div className="p-6 overflow-y-auto text-sm leading-relaxed whitespace-pre-wrap font-medium">{aiResult}</div>
              <div className="p-5 border-t">
                <Button className="w-full font-black h-12" onClick={() => setAiResult(null)}>확인</Button>
              </div>
            </Card>
          </div>
        )}
        
        {step === 'view' && exists && (
          <div className="space-y-6 animate-in fade-in duration-500">
            <div className="text-center mb-8">
              <div className="h-16 w-16 bg-primary/10 rounded-2xl flex items-center justify-center mx-auto mb-4">
                <CheckCircle2 className="h-8 w-8 text-primary" />
              </div>
              <h2 className="text-3xl font-black mb-1 tracking-tight italic uppercase">My Profile</h2>
              <p className="text-sm text-muted-foreground font-bold">{formData.job} | {formData.age}세</p>
            </div>

            <Button 
              className="w-full h-36 flex-col bg-primary hover:bg-primary/90 text-white shadow-xl border-none transition-all hover:scale-[1.02]"
              onClick={() => setStep('deep_analysis')}
            >
              <BrainCircuit className="h-8 w-8 mb-3 animate-pulse" />
              <span className="text-2xl font-black uppercase tracking-tighter">AI Analysis</span>
              <span className="text-[10px] opacity-80 mt-2 font-bold uppercase tracking-widest text-white">맞춤형 재무설계 보고서 생성</span>
            </Button>

            <div className="grid grid-cols-2 gap-3">
              <Button variant="outline" className="h-16 font-bold border-2" onClick={() => setStep('form')}>
                <Edit3 className="mr-2 h-4 w-4" /> 정보 수정
              </Button>
              <Button variant="outline" className="h-16 font-bold border-2 text-destructive" onClick={handleDeleteBaseGoal}>
                <Trash2 className="mr-2 h-4 w-4" /> 정보 삭제
              </Button>
            </div>

            <Card className="p-6 bg-secondary/10 border-none shadow-inner">
              <h3 className="text-[11px] font-black text-muted-foreground uppercase mb-4 flex items-center gap-1.5 tracking-widest">
                <Wallet className="h-3.5 w-3.5" /> Financial Stats
              </h3>
              <div className="grid grid-cols-2 gap-y-4 font-black">
                <div><p className="text-[10px] text-muted-foreground uppercase">총 자산</p><p className="text-xl">₩{Number(formData.capital).toLocaleString()}</p></div>
                <div><p className="text-[10px] text-muted-foreground uppercase">월 순수입</p><p className="text-xl text-primary">₩{(Number(formData.monthlyIncome)).toLocaleString()}</p></div>
              </div>
            </Card>
          </div>
        )}

        {step === 'history' && (
          <div className="space-y-6 animate-in slide-in-from-right-8 duration-500">
            <h2 className="text-2xl font-black flex items-center gap-2 italic uppercase"><History className="h-6 w-6 text-primary" /> History</h2>
            <div className="space-y-3">
              {reports.length > 0 ? reports.map((report) => (
                <Card key={report.reportId} className="p-4 bg-card border shadow-sm">
                  <div className="flex justify-between items-center">
                    <div className="cursor-pointer flex-1 pr-4" onClick={() => setAiResult(report.resultReport)}>
                      <p className="text-sm font-bold truncate italic uppercase tracking-tighter">{report.question?.split('.')[0] || "Financial Report"}</p>
                      <div className="flex items-center gap-2 text-[10px] text-muted-foreground mt-1 font-bold"><Clock className="h-3 w-3" /> {new Date(report.createdAt).toLocaleDateString()}</div>
                    </div>
                    <Button variant="ghost" size="icon" className="text-destructive h-8 w-8" onClick={() => handleDeleteReport(report.reportId)}><Trash2 className="h-4 w-4" /></Button>
                  </div>
                </Card>
              )) : <div className="text-center py-20 text-muted-foreground font-black opacity-30 uppercase">No Records</div>}
            </div>
          </div>
        )}

        {step === 'form' && (
          <div className="space-y-6 animate-in slide-in-from-bottom-6 duration-500">
            <h2 className="text-2xl font-black mb-2 tracking-tight text-center italic uppercase">Setup Profile</h2>
            <Card className="p-6 space-y-5 shadow-2xl bg-card border-none">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-1.5"><label className="text-[11px] font-black text-muted-foreground uppercase ml-1">나이</label>
                  <Input type="number" value={formData.age} onChange={e => setFormData({...formData, age: e.target.value})} className="h-12 font-bold bg-secondary/20 border-none" />
                </div>
                <div className="space-y-1.5"><label className="text-[11px] font-black text-muted-foreground uppercase ml-1">직업</label>
                  <Input value={formData.job} onChange={e => setFormData({...formData, job: e.target.value})} className="h-12 font-bold bg-secondary/20 border-none" />
                </div>
              </div>
              <div className="space-y-1.5"><label className="text-[11px] font-black text-muted-foreground uppercase ml-1">현재 총 자산 (원)</label>
                <Input type="number" value={formData.capital} onChange={e => setFormData({...formData, capital: e.target.value})} className="h-12 font-bold bg-secondary/20 border-none" />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-1.5"><label className="text-[11px] font-black text-muted-foreground uppercase ml-1">월 수입</label>
                  <Input type="number" value={formData.monthlyIncome} onChange={e => setFormData({...formData, monthlyIncome: e.target.value})} className="h-12 font-bold bg-secondary/20 border-none" />
                </div>
                <div className="space-y-1.5"><label className="text-[11px] font-black text-muted-foreground uppercase ml-1 text-destructive">월 고정 지출</label>
                  <Input type="number" value={formData.fixedExpenses} onChange={e => setFormData({...formData, fixedExpenses: e.target.value})} className="h-12 font-bold bg-secondary/20 border-none" />
                </div>
              </div>
              <Button className="w-full h-14 text-lg font-black mt-2 uppercase tracking-widest italic" onClick={handleSaveInfo} disabled={isSubmitting}>
                {isSubmitting ? <Loader2 className="animate-spin" /> : "Save Profile"}
              </Button>
            </Card>
          </div>
        )}

        {/* ... Deep Analysis Step은 기존과 동일 ... */}
      </main>
    </div>
  )
}